/*-------login-----*/
.login_wrapper {
  background: var(--bg-secondary);
}

.login_wrap {
  height: 100vh;
  color: var(--color-primary);
}
.login_1 {
  width: 100%;
  height: 100%;
  display: grid;  
  grid-template-columns: 100px 100px 100px;  
  grid-template-rows: 50px 50px; 
  background-color: rgba(77, 74, 199, 1);
}
@media screen and (max-width: 848px) {    .login_1 { display: none; } }


.login_1 span {
  font-size: 20px;
}
.login_2 {
  text-align: center;
  padding-left: 100px;
}
.login_2_wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  background: var(--bg-primary);
  box-shadow: 0 1px 2px var(--shadow-1);
  padding: 1rem;
  padding-bottom: 2rem;
  width: 350px;
  height: fit-content;
  margin: 1rem auto;
  user-select: none;
  border-radius: 10px;
}
.login_2_wrap button {
  width: 100%;
  height: 50px;
  font-size: 17px;
  font-weight: 700;
}
.forgot_password {
  color: var(--blue-color);
  font-size: 14px;
  cursor: pointer;
}
.forgot_password:hover {
  text-decoration: underline;
}
.sign_splitter {
  width: 100%;
  height: 1px;
}
.open_signup {
  border: 1px solid #BEA4A4;
  background: rgb(255, 255, 255);
  padding: 8.5px 12px;
  border-radius: 25px;
  font-family: inherit;
  font-weight: 600;
  font-size: 14px;
  color: #6F6C90;
  display: flex;
  align-items: center;
  font-weight: 600 !important;
  font-size: 17px !important;
  margin-top: 2rem;
}
.sign_extra {
  font-size: 15px;
}
/*-------login-----*/

/*-----Register---------*/
.register {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--bg-primary);
  box-shadow: 0 1px 2px var(--shadow-1);
  border-radius: 10px;
  padding: 15px;
  padding-bottom: 1rem;
  width: 350px;
  height: fit-content;
  color: var(--color-primary);
}
.register_header {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--bg-third);
}
.register_header i {
  position: absolute;
  right: 0;
  cursor: pointer;
}
.register_header span:first-of-type {
  font-weight: 700;
  font-size: 32px;
}
.register_header span:last-of-type {
  font-size: 15px;
  color: var(--color-secondary);
}
.register_form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.reg_line {
  padding: 7px 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.reg_col {
  position: relative;
  align-self: flex-start;
  margin-bottom: 10px;
  padding: 0 10px;
}
.reg_line_header {
  display: flex;
  align-items: center;
  gap: 2px;
  font-size: 13px;
  color: var(--color-secondary);
}
.reg_line_header i {
  margin-top: 3px;
}
.reg_grid {
  margin-top: 5px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  height: 35px;
  width: 100%;
}
.reg_grid select {
  width: 90px;
  font-size: 16px;
  color: var(--color-primary);
  background: var(--bg-primary);
  border-radius: 5px;
  cursor: pointer;
}
.reg_grid label {
  width: 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  color: var(--color-primary);
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid var(--color-secondary);
  padding: 0 10px;
}
.reg_infos {
  font-size: 11px;
  margin-top: 10px;
  color: var(--color-secondary);
}
.reg_infos span {
  color: var(--blue-color);
}
.reg_btn_wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0 10px 0;
}
/*-----Register---------*/


/*----dob and gender errors-----*/
.reg_grid .input_error {
  width: 300px;
  position: absolute;
  top: 70px;
}
.input_error_select_large {
  left: -97%;
  top: 15px !important;
}
/*----dob and gender errors-----*/

/*----error and success styles -------*/
.error_text {
  color: #b94a48;
}
.success_text {
  color: var(--green-color);
}
/*----error and success styles -------*/

/*----Media queries--------------*/
@media (min-width: 539px) {
 
  .register {
    width: 400px;
  }
  .reg_line {
    flex-direction: row;
  }
  .reg_grid select,
  .reg_grid label {
    width: 110px;
  }
  .reg_grid .input_error {
    width: 350px;
  }
}
@media (min-width: 650px) {
  .login_footer_wrap {
    max-width: 650px;
  }
}
@media (min-width: 850px) {
  .login_wrap {
    display: flex;
    align-items: center;
    max-width: 100%;
    margin: 0 auto;
  }
  .login_1 {
    display: flex;
    flex-direction: column;
    width: 50%;
  }
  .login_1 img {
    width: 60%;
    margin: auto;

  }
  .login_1 span {
    font-size: 28px;
  }
  .login_2_wrap {
    width: 400px;
    height: 600px;
  }
  .input_wrap {
    width: 360px !important;
  }
}
/*----Media queries--------------*/
